body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
      Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #f5d5f3;
    color: #212121;
    line-height: 1.5;
  }

#search-form {
    display: flex;
    justify-content: center;
    background: rgb(164, 4, 212);
    padding: 14px 10px;
    margin-bottom: 15px;
  
    box-shadow: 0 0 5px #50a23c;
  }

  img {
    display: block;
    margin: 0 auto;
    width: 356px;
    object-fit: cover;
    height: 225px;
  }


  .gallery-box {
    display: flex;
    flex-wrap: wrap;
    gap: 25px;
  }

  .info {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 0 4px rgba(245, 92, 247, 0.625);}

    .info-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 5px;
      color: black;
    
    }

    .gallery__link{
      text-decoration: none;
    }